@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700;900&display=swap);


/* prettier-ignore */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

/* prettier-ignore */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
  line-height: 1;
}

/* prettier-ignore */
ol, ul {
    list-style: none;
}
blockquote,
q {
  quotes: none;
}

/* prettier-ignore */
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

div {
  box-sizing: border-box;
}

* {
  font-family: 'Noto Sans KR', sans-serif !important;
  -webkit-text-size-adjust: none;
}

sup {
  vertical-align: super;
  font-size: smaller;
  line-height: 0.5;
}

var,
span {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

var::-webkit-scrollbar,
span::-webkit-scrollbar {
  display: none;
}

input::-ms-clear,
input::-ms-reveal {
  display: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: black !important;
}

html[color-mode='dark'] input:-webkit-autofill,
html[color-mode='dark'] input:-webkit-autofill:hover,
html[color-mode='dark'] input:-webkit-autofill:focus,
html[color-mode='dark'] input:-webkit-autofill:active {
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: white !important;
  caret-color: white;
}

html {
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  cursor: pointer;
}

button:focus {
  outline: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.react-responsive-modal-modal {
  max-width: 400px !important;
  padding: 30px !important;
  border-radius: 15px;
}

@media screen and (max-width: 960px) {
  .react-responsive-modal-modal {
    padding: 27px 30px !important;
    padding-bottom: 40px !important;
  }
}

@media screen and (max-width: 500px) {
  .react-responsive-modal-modal {
    padding: 25px 20px !important;
    padding-bottom: 40px !important;
  }
}

html[color-mode='dark'],
html[color-mode='dark'] body {
  background-color: #161920;
  color: white;
}

html[color-mode='dark'] .react-responsive-modal-modal {
  background-color: #212632;
}

* {
    -webkit-text-size-adjust: auto !important;
}
